.app {
  width: 100%;
  min-height: 100vh;
  min-height: 100svh;
  background-color: #FEF0D1;
  position: relative;
}
.printer {
  width: 200px;
  height: 600px;
  margin: 10px;
  background-color: #E3FA53;
  position: absolute;
  right: 0;
  top: 0;
  border-radius: 20px;
  border: #464749 4px solid;
  padding: 20px;
}
.printer>form {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  padding: 20px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.submit {
  width: calc(100% - 3px);
  margin: 5px;
}
.input {
  width: 150px;
  margin: 2px;
}
.label {
  display: inline-block;
  width: 30px;
  margin: 2px;
}
.text_data1 {
  margin-top: 200px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}
.text_data2 {
  margin-top: 110px;
}
.text_data3 {
  margin-top: 10px;
}
.joystick {
  position: absolute;
  right: 0;
  top:200px;
}
.name {
  transform: rotate(270deg);
}
.calc {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
}
.calc_row {
  display: flex;
  flex-direction: row;
}
.calc_btn {
  width: 20px;
  height: 20px;
  font-family: Arial, sans-serif;
  font-weight: 400;
  margin-right: 5px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
@media print {
  .printer{
    display: none;
  }
  .app {
    background-color: #fff;
  }
}
.label_full {
  background-color: #fff;
  width: 3cm;
  height: 20cm;
  border-bottom: #464749 dotted 1px;
  overflow: hidden;
}
.label_name_container {
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}
.label_name {
  transform: rotate(270deg);
}